@import url('https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,800;1,900&display=swap');
*{

    padding: 0;
    margin: 0;

}

:root{

    --primary-color: rgb(38, 38, 38);
    --secondary-color: black;

}

body{

    background-color: var(--primary-color);

}

.header{

    background-color: var(--secondary-color);
    height: 100px;
    width: 100%;
    display: flex;
    align-items: center;
    margin-bottom: 30px;
    position: relative;

}

.movie-site-wrapper{

    max-width: 1920px;
    justify-content: center;
    margin: auto;

}

.logo{

    position: absolute;
    left: 13rem;
    width: 65px;
    height: auto;

}

.site-name{

    color: white;
    font-size: 2.5rem;
    position: absolute;
    left: 19rem;
    font-family: 'Montserrat', sans-serif;

}

@media (max-width:901px){

    .site-name{

        display: none;
    }

    .logo{

        display: none;


    }

    .logo-small{

        margin-right: auto;
        margin-left: auto;

    }

}

@media (min-width: 902px){

    .logo-small{

        display: none;

    }
}


.page-navigation{

    word-spacing: 0.5rem;
    position: absolute;
    right: 0;
    padding-right: 12.25rem;
    font-size: 1.5rem;
    font-family: 'Source Sans Pro', sans-serif;
}

.navigation-header{

    background-color: var(--secondary-color);
    height: 50px;
    width: 80%;
    display: flex;
    justify-content: space-around;
    align-items: center;
    margin-bottom: 30px;
    border: solid white 1px;
    margin-bottom: 4rem;
    z-index: 100;

}

@media (max-width:901px){

    .navigation-header{
        
        background-color: var(--secondary-color);
        min-width: 527px;

    }
}

.navigation-header nav ul li a{

    font-family: 'Montserrat', sans-serif;
    font-weight: 100;

}

nav ul{

    display: flex;

}

nav ul li{

   list-style: none;
   margin-right: 1rem;

}

nav ul li a{

    text-decoration: none;
    color: white;
    font-size: 1.2rem;
    font-weight: bold;
    

}


nav ul li a:hover{

    color: grey;

}

nav ul li a::before{

    content: "";
    position: absolute;
    top: -10px;
    left: 50%;
    height: 3px;
    width: 100%;
    transform: translateX(-50%);
    background-color: var(--primary-color);
    transition: all 0.3s ease-in-out;

}

nav ul li a:hover:before{

    width:100%;
    
}

nav ul li a:focus{

    background-color: var(--primary-color);
    opacity: 0.2px;
    border-radius: 20px;

}

nav ul li a:focus::before{

    width: 0%;
    
}

.search-btn{

    display: flex;
    margin-top: 10px;

}


form{

    margin: 4rem;
    position: absolute;

}

.inputText{

    outline: none;
    border: 1px solid var(--primary-color);
    border-radius: 2rem 0 0 2rem;
    padding: 0.3rem 1rem;
    font-size: 1rem;
    color: white;
    background-color: var(--primary-color);
    box-shadow: inset 0 5px 10px var(--secondary-color);
    width: 600px;

}

.inputTexttwo{

    outline: none;
    border: 1px solid var(--primary-color);
    border-radius: 2rem 0 0 2rem;
    padding: 0.3rem 1rem;
    font-size: 1rem;
    color: white;
    background-color: var(--primary-color);
    box-shadow: inset 0 5px 10px var(--secondary-color);
    width: 300px;

}

.search-btn button{
    background-color: var(--secondary-color);
    box-shadow: inset 0 5px 10px var(--primary-color);
    padding: 0.2rem 0.5rem;
    border-radius: 0 2rem 2rem 0;
    color: white;
    font-size: 1rem;
    outline: none;
    border: 1px solid var(--primary-color);
}

.search-btn-two button{
    background-color: var(--secondary-color);
    box-shadow: inset 0 5px 10px var(--primary-color);
    padding: 0.2rem 0.5rem;
    border-radius: 0 2rem 2rem 0;
    color: white;
    font-size: 1rem;
    outline: none;
    border: 1px solid var(--primary-color);
}

.search-btn-two{

    position: absolute;
    right: 13rem;

}

.container{
    margin: auto;
    width: 80%;
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
}

.movie{

    box-shadow: 0 5px 10px black;
    width: 250px;
    background-color: rgb(188, 188, 188);
    margin: 1rem;
    border-radius: 5px;
    box-sizing: border-box;
    overflow: hidden;
    position: relative;

}

.movie .poster{

    width: 100%;
    height: auto;

}

.movie-details{

    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

}

.movie-details .box{

    width: 100%;
    padding: 1rem;
    display: flex;
    align-items: center;
    justify-content: space-around;

}

.movie-details .title{

    width: 160px;
    text-align: center;

}

.movie-details .overview{

    position: absolute;
    left: 0;
    bottom: 0;
    height: 100%;
    background-color: var(--secondary-color);
    font-family: 'Montserrat', sans-serif;
    opacity: 0;
    color: white;
    box-sizing: border-box;
    padding: 1rem;
    overflow-y: auto;

}

.movie-details:hover .overview{

    transition: 0.3s;
    opacity: 0.9;

}

.overview h1{

    padding-bottom: 1rem;

} 

.overview p{

    font-weight: 100;
    font-size: 0.75rem;

}   

.rating{

    background-color: var(--primary-color);
    width: 35px;
    height: 35px;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    font-weight: bold;
    border: 1px solid var(--primary-color);
    box-shadow: inset 0 5px 5px rgba(0,0,0,0.5);
    position: absolute;
    right: 5px;
    bottom: 5px;
    color: white;

}

@media screen and (min-width:705px){

    .navigation-header nav ul li a {

        padding: 0.2rem 0.6rem;
    
    }
}

@media screen and (max-width:705px){

    .search-btn{

        width: 200px;
        margin-right: 100px;
        position: relative;
        right: -1.75rem;
        
    }

    .navigation-header {

        width: 280px;
        min-width: 220px;

    }

    .navigation-header nav ul li a {
    
        font-size: 0.65rem;
        white-space: nowrap;
        position: relative;
        right: -0.45rem;

    }

    .search-btn {
        position: relative;
        top: -1rem;
    }

    .navigation-header nav ul li a {
    
        font-size: 0.65rem;
    
    }
    
    .navigation-header{
    
        height: 30px;
    
    }

}

.notfound{

    font-size: 40px;
    font-weight: bold;
    position: absolute;
    margin-top: 13rem;
    color: white;

}

footer{

    color: white;
    font-family: 'Montserrat', sans-serif;
    font-weight: 100;
    text-align: center;
    background-color: black;
    padding-top: 2rem;
    padding-bottom: 2rem;
    margin-top: 30rem;
    width: 100%;


}

@media screen and (max-width:355px){

    .navigation-header nav ul li a {
        
        font-size: 0.59rem;
    
    }

}


@media screen and (min-width: 1774px){

    .navigation-header{

        width: 82%;

    }

}

@media screen and (min-width: 1230px){

    .search-btn{

        display: none;

    }

    .navigation-header{

        position: relative;
        bottom: -1.5rem;

    }

}

@media screen and (max-width: 1230px){

    .logo{

        display: none;

    }

    .site-name{

        display: none;

    }

    .logo-small{

        display: block;
        width: 65px;
        height: auto;
        margin-right: auto;
        margin-left: auto;

    }

    .search-btn-two{

        display: none;

    }

}

@media screen and (max-width: 1011px){

    .inputText{

        width: 480px;
    
    }

}

@media screen and (max-width: 825px){

    nav ul li a {

        font-size: 1rem;

    }
}